.box {
  display: block;
  margin-top: 30px;
}

.content {
  display: block;
  width: 100%;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 10px 20px 10px #3333330f;
}

.section {
  width: 100%;
  padding-inline: 30px;
  padding-block: 30px;
}

.title {
  font-size: 30px;
  font-family: "Titillium Web";
  font-weight: 500;
  border-bottom: 2px solid #eeeff1;
  display: inline-block;
}

.inner {
  width: 100%;
  display: block;
}
