.box {
  width: 22%;
  margin-top: 30px;
}

.nav {
  display: block;
  width: 100%;
  border-radius: 10px;
  background: #fff;
  box-shadow: 0px 10px 20px 10px #3333330f;
  position: sticky;
  top: 10px;
}

.list {
  display: block;
  width: 100%;
  padding: 20px;
}

.list li {
  display: block;
}

.list li a {
  display: block;
  padding: 9px;
  border-radius: 5px;
  margin-bottom: 7px;
  font-family: "Titillium Web";
  font-size: 16px;
  font-weight: 300;
  transition: 0.2s all;
  position: relative;
}

.current {
  background: #eeeff1;
  color: #000;
  transition: 0.2s all;
}

.list li a:hover {
  background: #0072ff;
  color: #fff;
  transition: 0.2s all;
}

.current:hover {
  background: #eeeff1 !important;
  color: #000 !important;
  transition: 0.2s all;
}

.list li a i {
  position: absolute;
  right: 5px;
  top: 50%;
  transform: translate(-50%, -50%);
}
