.list li {
  list-style: none;
  background: #87afff0f;
  margin-left: 0;
  padding-block: 16px;
  padding-inline: 20px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  box-sizing: border-box;
  margin-right:10px ;
}

.list li i {
  margin-right: 10px;
  color: #0058c5;
  font-size: 21px;
}

.list li span {
  color: #0855b3;
  font-size: 14px;
  font-weight: 600;
  font-family: "Titillium Web";
}
