.main {
  display: block;
}

.text {
  padding-block: 10px;
  font-size: 15px;
  font-family: "Open Sans";
  margin-bottom: 10px;
  margin-top: 10px;
}

.list {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  flex-wrap: wrap;
}
.section {
  display: block;
}
.title {
  display: block;
  font-size: 16px;
  font-weight: 600;
  font-family: "Titillium Web";
}

.innerList {
  display: block;
  padding: 0;
  margin-block: 5px;
}

.innerList li {
  display: flex;
  line-height: 30px;
  font-family: "Titillium Web";
}

.innerList li a {
  font-size: 11px;
  display: inline-block;
  color: #d6d6d6;
  margin-left: 10px;
}

.innerList li a:hover {
  color: #333;
}
