.main {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.skill {
  padding-right: 4%;
  margin-top: 30px;
  animation-name: skill;
  animation-duration: 0.5s;
  animation-fill-mode: both;
  animation-iteration-count: 1;
  animation-timing-function: ease-in-out;
  opacity: 0;
}
@for $i from 1 to 20 {
  .skill:nth-child(#{$i}) {
    animation-delay: $i * 0.15s;
  }
}

@keyframes skill {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.title {
  display: block;
}
.title span:first-child {
  display: inline-block;
  font-family: "Titillium Web";
  font-size: 14px;
  font-weight: 500;
}
.title span:nth-child(2) {
  display: inline-block;
  float: right;
  font-family: "Titillium Web";
  font-size: 14px;
  font-weight: 700;
  color: #ccc;
  margin-top: 5px;
}

.bar {
  display: block;
  width: 100%;
  height: 4px;
  background: #eeeff1;
  margin-block: 12px;
}

.bar span {
  display: block;
  width: 80%;
  background: #0072ff;
  height: 4px;
}
