.main {
  display: block;
}
.section {
  padding-right: 0px;
  padding-block: 30px;
}
.sectionTitle {
  width: 100%;
  background: #f8faff;
  color: #0855b3;
  font-size: 14px;
  font-weight: 600;
  font-family: "Titillium Web";
  padding: 15px;
  text-align: center;
}
.sectionContent {
  display: block;
  padding-block: 20px;
}

.bookitem {
  display: block;
  // height: 140px;
  margin-bottom: 10px;
}

.cover {
  width: 100%;
  height: 100%;
  text-align: center;
  overflow: hidden;
  position: relative;
}

.cover img {
  border-radius: 5px;
  height: 100%;
  width: inherit;
  transition: 0.2s all;
}

.cover i {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40px;
  height: 40px;
  background: #000000af;
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s all;
}

.bookitem:hover .cover i {
  opacity: 1;
  visibility: visible;
  transition: 0.2s all;
}

.bookitem:hover .cover img {
  filter: grayscale(1);
  background: #333;
  transition: 0.2s all;
}
