.box {
  margin-top: 50px;
  position: relative;
}
.item {
  display: block;
  border-left: 2px solid #000000;
  margin-left: 20px;
  padding-inline: 50px;
  position: relative;
  padding-bottom: 50px;
  padding-top: 0px;
}
.id {
  position: absolute;
  left: 0;
  width: 50px;
  height: 50px;
  border: 2px solid #000000;
  text-align: center;
  display: flex;
  padding-top: 5px;
  justify-content: center;
  align-content: center;
  font-family: "Titillium Web";
  font-weight: 300;
  font-size: 23px;
  border-radius: 100%;
  color: #555;
  background: #fff;
  transform: translate(-50%, -10%);
  transition: 0.2s all;
}

.item:hover .id {
  color: #fff;
  background: #111;
  transition: 0.2s all;
}

.header {
  display: block;
}
.title {
  display: inline-block;
  font-family: "Titillium Web";
  font-weight: 700;
  color: #444;
  font-size: 18px;
}
.cname {
  font-family: "Titillium Web";
  font-weight: 700;
  color: #9e9e9e;
  font-size: 16px;
  margin-bottom: 10px;
  transition: 0.2s all;
}
.date {
  display: inline-block;
  font-family: "Titillium Web";
  font-weight: 700;
  color: #888;
  font-size: 17px;
}
.text {
  display: block;
  text-align: justify;
  font-size: 14px;
  font-family: "Open Sans";
  line-height: 32px;
}

.item:hover .cname {
  color: #0072ff;
  transition: 0.2s all;
}


.idz {
  position: absolute;
  left: 20px;
  width: 50px;
  height: 50px;
  border: 2px solid #000000;
  text-align: center;
  display: flex;
  padding-top: 5px;
  justify-content: center;
  align-content: center;
  font-family: "Titillium Web";
  font-weight: 300;
  font-size: 23px;
  border-radius: 100%;
  color: #555;
  background: #fff;
  transform: translate(-50%, -10%);
  transition: 0.2s all;
}

.idz:hover{
  color: #fff;
  background: #111;
  transition: 0.2s all;
}

.idz i{
  text-align: center;
  display: flex;
  width: 100%;
  justify-content: center;
  align-content: center;
  position: absolute;
  left: 50%;
  top: 50%;
  font-size: 25px;
  transform: translate(-50%, -50%);
  transition: 0.2s all;
}