.header {
  padding-block: 30px;
  border: 1px solid #eee;
}
.left {
  padding-inline: 30px;
}
.right {
  padding-inline: 30px;
}

.picture {
  display: block;
  width: 100%;
  margin-bottom: 15px;
}

.picture img {
  display: block;
  border-radius: 5px;
  filter: grayscale(1) contrast(1.1);
  transition: 0.2s all;
}

.picture img:hover {
  filter: grayscale(0) contrast(1.1);
  transition: 0.2s all;
}

.name {
  display: block;
}
.name span:nth-child(1) {
  display: block;
  font-family: "Titillium Web";
  font-size: 40px;
  font-weight: 200;
  line-height: 46px;
}
.name span:nth-child(2) {
  display: block;
  font-family: "Titillium Web";
  font-size: 40px;
  font-weight: 600;
  line-height: 46px;
  transition: 0.2s all;
}
.role {
  display: block;
  margin-top: 10px;
}
.role span {
  background: #0072ff;
  color: #fff;
  font-family: "Titillium Web";
  font-size: 17px;
  font-weight: 100;
  padding-block: 3px;
  padding-inline: 20px;
  border-radius: 3px;
}

.about {
  display: block;
  font-size: 15px;
  font-family: Roboto;
  font-weight: 300;
  text-align: justify;
  line-height: 33px;
  margin-bottom: 25px;
}
.social {
  display: flex;
  flex-direction: row;
  align-content: center;
  justify-content: space-around;
  align-items: center;
}
.social a {
  text-align: center;
  display: flex;
  align-content: center;
  align-items: center;
  justify-content: center;
  position: relative;
}

.social a:first-child {
  border-top-left-radius: 5px;
}

.social a li {
  display: block;
}
.social a li i {
  display: block;
  font-size: 30px;
}
.social a li span {
  bottom: 70%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: max-content;
  position: absolute;
  background: #eeeff1;
  color: #000;
  font-size: 13px;
  padding-block: 6px;
  padding-inline: 27px;
  border-radius: 5px;
  font-family: "Titillium Web";
  font-weight: 500;
  padding-bottom: 8px;
  opacity: 0;
  visibility: hidden;
  transition: 0.2s all;
}

.social a:hover li span {
  bottom: 100%;
  opacity: 1;
  visibility: visible;
  transition: 0.2s all;
}

.dl {
  display: inline-block;
  background: #0072ff14;
  padding-block: 10px;
  padding-inline: 20px;
  color: #0072ff;
  font-family: "Titillium Web";
  border-radius: 4px;
  font-weight: 700;
}
.dl span {
}

.dl i {
  font-size: 17px;
  margin-right: 7px;
}

.contact {
  display: flex;
  justify-content: flex-start;
}
.contactitem {
  display: flex;
  margin-right: 40px;
  align-items: center;
  align-content: center;
  justify-content: flex-start;
  margin-bottom: 30px;
}
.contactitem i {
  display: inline-block;
  font-size: 21px;
}
.contactitem span {
  display: inline-block;
  font-size: 15px;
  font-family: "Titillium Web";
  margin-left: 6px;
  font-weight: 600;
}

.about strong {
  font-weight: 500;
}
