.list {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-wrap: wrap;
}

.item {
  display: flex;
  align-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  justify-content: space-around;
  padding-bottom: 20px;
  padding-top: 20px;
  border-bottom: 1px solid #eee;
}

.left {
  width: 15%;
}
.right {
  width: 85%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.image {
}

.image img {
  border-radius: 5px;
}

.title {
  display: inline-block;
  font-family: "Titillium Web";
  font-weight: 700;
  color: #444;
  font-size: 17px;
  margin-left: 10px;
}
.bottom {
  display: flex;
  justify-content: space-between;
}
.link {
  margin-left: 10px;
  font-family: "Titillium Web";
  font-weight: 700;
  color: #075fcc;
  font-size: 15px;
}
.date {
  display: inline-block;
  font-family: "Titillium Web";
  font-weight: 600;
  color: #bababa;
  font-size: 14px;
}

.subtitle {
  display: inline-block;
  font-family: "Titillium Web";
  font-weight: 700;
  color: #9b9b9c;
  font-size: 13px;
  margin-left: 10px;
  margin-bottom: 10px;
}
